<template>
	<view class="page bgwhite">
		<view class="list">
			<u--form ref="uform" :model="form" :rules="rules" label-width="110px">
				<u-form-item :label="$t('机器编号')" style="padding-left: 10px;" prop="machineNo">
					<u--input v-model="form.machineNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('工作中心')" style="padding-left: 10px;" prop="wsCode">
					<u--input v-model="form.wsCode" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('开始时间')" style="padding-left: 10px;" prop="startDate">
					<view class="itemView">
						<u--input v-model="form.startDate " :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399"
							style="height: 20px;margin-right: 0px;" @focus="sproductTimeShow = true"></u--input>
						<u-datetime-picker :show="sproductTimeShow" v-model="valueDate" mode="datetime"
							:formatter="formatter" @cancel="sproductTimeShow=false" @confirm="stimeConfirm"
							@change="stimeChangeHandler" :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>
				<u-form-item :label="$t('结束时间')" style="padding-left: 10px;" prop="endtDate">
					<view class="itemView">
						<u--input v-model="form.endDate " :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399"
							style="height: 20px;margin-right: 0px;" @focus="eproductTimeShow = true"></u--input>
						<u-datetime-picker :show="eproductTimeShow" v-model="valueEndDate" mode="datetime"
							:formatter="formatter" @cancel="eproductTimeShow=false" @confirm="etimeConfirm"
							@change="etimeChangeHandler" :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>
			</u--form>
		</view>

		<u-button class="confirmBtn" shape="circle" :text="$t('screening')" @click="submit"></u-button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					machineNo: null,
					startDate: null,
					endDate: null,
					wsCode: null,
				},
				rules: {},
				valueDate: Number(new Date()),
				valueEndDate: Number(new Date()),
				sproductTimeShow: false,
				eproductTimeShow: false,
				productTime: null,
				productEndTime: null,
				selectIndex: 0,
				classesList: [
					["白班", "中班", "夜班"]
				]
			}
		},
		onShow() {
			uni.setNavigationBarTitle({ // 修改头部标题
				title: this.$t("stop.report.screening")
			});
		},
		methods: {
			submit() {
				this.$refs.uform.validate(valid => {
					if (valid) {
						console.log('验证通过');
					} else {
						console.log('验证失败');
					}
				});
				
				uni.$emit('returnTypeData', this.form)
				uni.navigateBack({
					delta: 1
				});
			},

			stimeChangeHandler(e) {
				console.log("选择的时间=", e.value)
				this.productTime = e.value
			},
			etimeChangeHandler(e) {
				console.log("选择的时间=", e.value)
				this.productEndTime = e.value
			},
			stimeConfirm() {
				this.sproductTimeShow = false
				this.form.startDate = this.timestampToTime(this.productTime ? this.productTime : this.valueDate);
			},
			etimeConfirm() {
				this.eproductTimeShow = false
				this.form.endDate = this.timestampToTime(this.productEndTime ? this.productEndTime : this.valueEndDate);
			},
			//时间戳转时间
			timestampToTime(value) {
				let date = new Date(value);
				let y = date.getFullYear(); // 年
				let MM = date.getMonth() + 1; // 月
				MM = MM < 10 ? ('0' + MM) : MM;
				let d = date.getDate(); // 日
				d = d < 10 ? ('0' + d) : d;
				let h = date.getHours(); // 时
				h = h < 10 ? ('0' + h) : h;
				let m = date.getMinutes(); // 分
				m = m < 10 ? ('0' + m) : m;
				let s = date.getSeconds(); // 秒
				s = s < 10 ? ('0' + s) : s;
				return y + '-' + MM + '-' + d + " " + h + ':' + m + ':' + s;
			},

			formatter(type, value) {
				if (type === 'year') {
					return `${value}年`
				}
				if (type === 'month') {
					return `${value}月`
				}
				if (type === 'day') {
					return `${value}日`
				}
				if (type === 'hour') {
					return `${value}时`
				}
				if (type === 'minute') {
					return `${value}分`
				}
				return value
			},
		}
	}
</script>

<style lang="scss" scoped>
	.list {
		&-item {
			padding: 10upx 16upx;
			@include after();
			font-size: 28upx;
			border: 0;
			justify-content: center;
			align-items: center;

			.label {
				width: 200upx;
				color: #333;
				font-weight: 500;
			}

			.value {
				color: #666;
			}
		}
	}

	.itemView {
		width: 100%;
		margin-right: 10px;
	}

	.confirmBtn {
		width: 80%;
		margin-top: 20px;
		margin-bottom: 20px;
		align-items: center;
		color: white;
		font-size: 20px;
		background-color: $ch-theme;
	}
</style>